import { Component, OnInit } from '@angular/core';
import { fromEvent, Observable, Subscription } from 'rxjs';
import {  throttleTime, scan } from 'rxjs/operators';

@Component({
  selector: 'app-counter',
  templateUrl: './counter.component.html',
  styleUrls: ['./counter.component.scss']
})
export class CounterComponent implements OnInit {

  constructor() { }
  clickObs$:Observable<Event>
  clickSubs:Subscription
  publicCount=0
  
  ngOnInit(): void {
    this.initCounterEvent()
    this.initResetEvent()
  }

  initCounterEvent(){
    this.clickObs$= fromEvent(document.querySelector("#buttonCounter"),"click")
    this.clickSubs = this.clickObs$
    .pipe(
      throttleTime(1000),
      scan(count => {
        this.publicCount=count++ ; 
        return count
      }, 0)
    )
    .subscribe(
      {
        next: count => this.publicCount=count,
        error: err => console.log(err),
        complete: () => this.clickSubs.unsubscribe()
      }
    )
  }

  initResetEvent(){
    const resetObs$= fromEvent(document.querySelector("#buttonReset"),"click")
    .pipe(
      throttleTime(5000),
    )
    resetObs$.subscribe(
      __ => {
        this.clickSubs.unsubscribe()
        this.publicCount = 0
        this.initCounterEvent()
      }
    )
  }
}
