import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { PokemonService } from 'src/app/services/pokemon.service';
import { fromEvent, Observable, Subscription } from 'rxjs';
import { throttleTime } from 'rxjs/operators';

@Component({
  selector: 'app-pokemon',
  templateUrl: './pokemon.component.html',
  styleUrls: ['./pokemon.component.scss']
})
export class PokemonComponent implements OnInit {

  @ViewChild('inputPokemon') inputPokemon:ElementRef
  @ViewChild('buttonPokemon') buttonPokemon:ElementRef

  searchButton$:Observable<Event>
  searchButtonSubs:Subscription

  textBox$:Observable<Event>
  textBoxSubs:Subscription

  currentText:string = ""

  pokemonResult

  displayedColumns: string[] = ['name', 'base', 'effort'];

  constructor(private pokemonService:PokemonService) { }

  ngOnInit(): void {}

  ngAfterViewInit(): void {
    this.initSearchEvent()
    this.initTextEvent()
  }

  ngOnDestroy(){

  }

  initSearchEvent(){
    this.searchButton$= fromEvent(this.buttonPokemon.nativeElement,"click")
    this.searchButtonSubs = this.searchButton$
    .pipe(
      throttleTime(5000),
    )
    .subscribe(
        __ =>{
          if(this.inputPokemon.nativeElement.value){
            this.currentText = this.inputPokemon.nativeElement.value.toLowerCase();
            this.subscribePokemonService(this.currentText)
          }
          else{
            console.log("fill the input")
          }
        }
    )
  }

  initTextEvent(){
    this.textBox$= fromEvent(this.inputPokemon.nativeElement,"change")
    this.textBoxSubs = this.textBox$
    .subscribe(
      __=> this.currentText = this.inputPokemon.nativeElement.value
    )
  }

  subscribePokemonService(text){

    let isValid = RegExp(/([0-9]|[a-zA-Z])+/).test(text)
    if(!isValid){
      console.log("Use only letters or numbers!")
      return
    }
    const response$ = this.pokemonService.getPokemon(text)
    let subscription = response$.subscribe({
      next:(pokemon)=>{
          this.pokemonResult = {
            pokemonImage : pokemon.sprites.other["official-artwork"].front_default,
            pokemonName : pokemon.name,
            pokemonType : pokemon.types,
            pokemonStats : pokemon.stats
          }
        },
      error: err=>console.log(err),
      complete: ()=>{
        console.log("consultado...");
        subscription.unsubscribe()
      }
    })
  }
}
